"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2, Clock
} from "lucide-react"

// --- 인터페이스 ---
interface Promotion {
  id: string; 
  name: string;        
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  etc: string | null;
  createdAt: string; // 생성일 필드
}

interface AdvisorRequest {
  userId: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  // API 주소
  const PROMO_API = "http://34.50.7.8:3000/v1/promotions"
  const USER_API = "http://34.50.7.8/v1/user"

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)

  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=5`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content)
        setTotalPromoPages(data.result.totalPages)
      }
    } catch (error) {
      console.error("데이터 로드 실패", error)
    } finally {
      setIsLoading(false)
    }
  }, [promoPage])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${USER_API}/admin/advisor/pending`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) setAdvisorRequests(data.result)
    } catch (e) { console.error(e) }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role)
    setUserName(name)
    setIsMounted(true)
    fetchPromotions()
    fetchPendingAdvisors()
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm("상태를 변경하시겠습니까?")) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (e) { alert("오류 발생") }
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={100} height={28} className="h-7 w-auto" />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold uppercase">{userRole} ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <p className="text-sm font-bold">{userName}님</p>
            <Button variant="ghost" size="icon" onClick={() => {localStorage.clear(); router.push("/login")}} className="rounded-xl"><LogOut className="h-5 w-5" /></Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-border rounded-2xl"><Activity className="h-5 w-5 text-primary mb-2" /><p className="text-xs font-black text-muted-foreground uppercase italic">System</p><p className="text-2xl font-black">Operational</p></Card>
          <Card className="p-6 border-border rounded-2xl"><Zap className="h-5 w-5 text-accent mb-2" /><p className="text-xs font-black text-muted-foreground uppercase italic">Running</p><p className="text-2xl font-black">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p></Card>
          <Card className="p-6 border-border rounded-2xl"><UserCheck className="h-5 w-5 text-orange-500 mb-2" /><p className="text-xs font-black text-muted-foreground uppercase italic">Pending</p><p className="text-2xl font-black">{advisorRequests.length} 명</p></Card>
        </div>

        <Tabs defaultValue="promotions">
          <TabsList className="bg-secondary/30 mb-6 p-1 rounded-xl">
            <TabsTrigger value="promotions" className="font-bold rounded-lg px-8 uppercase italic">Promotions</TabsTrigger>
            <TabsTrigger value="advisors" className="font-bold rounded-lg px-8 uppercase italic">Advisors</TabsTrigger>
          </TabsList>

          <TabsContent value="promotions">
            <Card className="p-6 border-border shadow-none rounded-2xl bg-card/50">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-black italic uppercase flex items-center gap-2"><Sparkles className="h-5 w-5 text-primary" /> Campaign List</h2>
                <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl font-bold font-sans"><Plus className="h-4 w-4 mr-1" /> 신규 등록</Button>
              </div>

              <div className="overflow-x-auto">
                {isLoading ? <div className="py-20 flex justify-center"><Loader2 className="animate-spin text-primary" /></div> : (
                  <table className="w-full text-left">
                    <thead className="text-muted-foreground text-[10px] font-black uppercase border-b border-border/50 tracking-widest">
                      <tr>
                        <th className="px-2 py-3">Promotion Info</th>
                        <th className="px-2 py-3">Created At</th> {/* 추가된 헤더 */}
                        <th className="px-2 py-3 text-center">Status</th>
                        <th className="px-2 py-3 text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-border/30 text-sm">
                      {promotions.map((p) => (
                        <tr key={p.id} className="group hover:bg-secondary/10 transition-all">
                          <td className="px-2 py-4">
                            <p className="font-black text-foreground italic uppercase text-base">{p.name}</p>
                            <p className="text-xs text-primary font-bold font-mono tracking-tighter">
                              {p.pointAmount.toLocaleString()}P · {p.totalStock}명
                            </p>
                          </td>
                          <td className="px-2 py-4">
                            <div className="flex items-center gap-1.5 text-muted-foreground">
                              <Clock className="h-3 w-3 text-primary/60" />
                              <div className="flex flex-col">
                                <span className="text-[11px] font-black font-mono leading-none tracking-tighter">
                                  {p.createdAt.split('T')[0]}
                                </span>
                                <span className="text-[9px] font-bold opacity-60">
                                  {p.createdAt.split('T')[1].split('.')[0]}
                                </span>
                              </div>
                            </div>
                          </td>
                          <td className="px-2 py-4 text-center">
                            <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded-md font-black uppercase text-[9px]">
                              {p.status}
                            </Badge>
                          </td>
                          <td className="px-2 py-4 text-right flex justify-end gap-1">
                            {p.status === "SCHEDULED" && (
                              <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-4 w-4 fill-current" /></Button>
                            )}
                            {p.status === "ACTIVE" && (
                              <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-4 w-4 fill-current" /></Button>
                            )}
                            <Button size="icon" variant="ghost" className="h-8 w-8 text-muted-foreground"><Info className="h-4 w-4" /></Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                )}
              </div>
              
              <div className="flex items-center justify-center gap-2 mt-6 pt-4 border-t border-border/50">
                <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.max(0, p-1))} disabled={promoPage === 0}><ChevronLeft className="h-4 w-4" /></Button>
                <span className="text-[10px] font-black px-4 italic">{promoPage + 1} / {totalPromoPages}</span>
                <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.min(totalPromoPages - 1, p+1))} disabled={promoPage >= totalPromoPages - 1}><ChevronRight className="h-4 w-4" /></Button>
              </div>
            </Card>
          </TabsContent>
          
          <TabsContent value="advisors">
             {/* 전문가 심사 리스트 (기존과 동일) */}
             <Card className="p-6 border-border rounded-2xl min-h-[400px]">
                <h2 className="text-xl font-black italic uppercase mb-6 flex items-center gap-2"><UserCheck className="h-5 w-5 text-primary" /> Review Queue</h2>
                <div className="grid gap-3">
                  {advisorRequests.map(req => (
                    <div key={req.userId} className="flex items-center justify-between p-4 rounded-xl bg-secondary/10 border border-transparent hover:border-primary/20 transition-all">
                      <div className="flex items-center gap-4">
                        <Avatar className="h-10 w-10 border border-white"><AvatarFallback className="font-bold bg-primary text-white">{req.name?.[0]}</AvatarFallback></Avatar>
                        <div><p className="font-bold text-sm tracking-tight">{req.name}</p><p className="text-[10px] text-muted-foreground font-mono">{req.email}</p></div>
                      </div>
                      <Button variant="outline" size="sm" className="h-8 font-black italic text-[10px] rounded-lg uppercase" onClick={() => {setSelectedRequest(req); setIsDetailOpen(true)}}>View Detail</Button>
                    </div>
                  ))}
                </div>
             </Card>
          </TabsContent>
        </Tabs>
      </main>

      {/* 심사 모달 (기존 유지) */}
      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="sm:max-w-[480px] rounded-[2rem] p-8 border-none bg-card shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic text-2xl uppercase tracking-tighter">Qualification</DialogTitle></DialogHeader>
          {selectedRequest && (
            <div className="space-y-5 py-4">
              <div className="flex items-center gap-4 p-5 rounded-2xl bg-secondary/20 border border-border/50">
                <Avatar className="h-14 w-14 border-4 border-white shadow-md"><AvatarFallback className="font-black text-xl bg-primary text-white">{selectedRequest.name?.[0]}</AvatarFallback></Avatar>
                <div><p className="font-black text-xl italic uppercase tracking-tight leading-none">{selectedRequest.name}</p><p className="text-[10px] text-muted-foreground font-mono uppercase mt-1 tracking-tighter">{selectedRequest.email}</p></div>
              </div>
              <div className="p-6 bg-secondary/5 border border-border/50 rounded-2xl text-[14px] leading-relaxed italic font-bold">"{selectedRequest.reason}"</div>
            </div>
          )}
          <DialogFooter className="gap-3">
            <Button variant="outline" className="flex-1 font-black h-12 rounded-xl border-2 uppercase italic text-xs" onClick={() => setIsDetailOpen(false)}>Close</Button>
            <Button className="flex-1 font-black h-12 rounded-xl shadow-lg shadow-primary/30 uppercase italic text-xs">Approve Access</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}